<template>
  <div class="home">
    <HomeHeading/>
    <HomeBanner/>
    <HomeWhy/>
    <Banner2Home/>
    <HomeDelivery/>
    <HomeReview/>
    <HomeContact/>
    <ButtonToTop v-if="this.showTopBtn"/>
  </div>
</template>

<script>
import HomeHeading from '@/components/HomeHeading.vue'
import HomeBanner from '@/components/HomeBanner.vue'
import HomeWhy from '@/components/HomeWhy.vue'
import Banner2Home from '@/components/Banner2Home.vue'
import HomeReview from '@/components/HomeReviews.vue'
import ButtonToTop from '@/components/ButtonToTop.vue'
import HomeDelivery from '@/components/HomeDelivery.vue'
import HomeContacts from '@/components/HomeContact.vue'

export default {
  data() {
    return {
      showTopBtn: false,
    }
  },
  components: {
    'HomeHeading': HomeHeading,
    'HomeBanner': HomeBanner,
    'HomeWhy': HomeWhy,
    'Banner2Home': Banner2Home,
    'HomeReview': HomeReview,
    'ButtonToTop': ButtonToTop,
    'HomeDelivery': HomeDelivery,
    'HomeContact': HomeContacts,
  },
  methods: {
    handleScroll() {
      if (window.scrollY > 300) {
        this.showTopBtn = true;
      } else {
        this.showTopBtn = false;
      }
    }
  },
  created () {
    window.addEventListener('scroll', this.handleScroll);
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll);
  }
}
</script>

<style lang="scss">
.home {
  width: 100%;
  background: #2c2d30;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}
</style>